<template>
    <div v-loading="creating">
        <el-input
            @keyup.enter.native="search"
            placeholder="请输入内容"
            prefix-icon="el-icon-search"
            clearable
            v-model="input">
        </el-input><span v-for="i in 5" :key="i">&nbsp;</span>
        <el-button type="primary" icon="el-icon-search" circle @click="search"></el-button>
        <el-divider></el-divider>
        <ul>
            <li v-for="(item, index) in title" class="infinite-list-item" :key="index" @click="getDetail(index)">
            <el-card class="card0" shadow="hover" :style="cardStyleList[index%7]">
               {{item}}
            </el-card>
            </li>
        </ul>
        <div style="text-align:center; color:grey;">
        <p v-if="!num"><strong>没 有 哦 ...</strong></p>
        </div>
    </div>
</template>

<script>
export default {
  data() {
    return {
      creating: false,
      input: '',
      title:[],
      code:[],
      num: 0,
      cardStyleList: [
            "background: linear-gradient(90deg, #3C3B3F,#605C3C);",
            "background: linear-gradient(90deg, #4CB8C4,#3CD3AD);",
            "background: linear-gradient(90deg, #DD5E89,#F7BB97);",
            "background: linear-gradient(90deg, #EB3349,#F45C43);",
            "background: linear-gradient(90deg, #11998e,#38ef7d);",
            "background: linear-gradient(90deg, #1488CC,#2B32B2);",
            "background: linear-gradient(90deg, #16A085,#F4D03F);"
      ],
    }
  },

  methods:{
      search: function(){
          if(this.input == ''){
            this.$message({
            message: '不能为空！！',
            type: 'warning'
          })
            if(this.num != 0){
                this.code.splice(0, this.num)
                this.title.splice(0, this.num)
                this.num = 0
            }
          return
          }
          this.creating = true
          console.log(this.$store)
          fetch('http://localhost:5555/search',{
              method:'POST',
              mode:'cors',
              body:JSON.stringify({
                  'keyword':this.input
              })
          }).then(res => res.json())
              .then(data => {
                    if(this.num != 0){
                        this.code.splice(0, this.num)
                        this.title.splice(0, this.num)
                    }
                    this.code.push.apply(this.code, data['code'])
                    this.title.push.apply(this.title, data['title'])
                    this.num = data['nums']
                    setTimeout(() => {this.creating = false}, 200)
              })
      },

      getDetail: function(i){
          this.$router.push({
              path: '/detail',
              query:{
                  code: this.code[i],
                  title: this.title[i]
              }
          })
      },
  }
}
</script>

<style>
.el-input__inner{
    border-color: rgb(101, 166, 226);
}
.el-input{
    
    margin-left: 10%;
    width: 70%;
}

.card0{
    text-align: center;
    line-height: 160px;
    border-radius: .3125rem;
    font-size: 1.5625rem;
    font-weight: 400;
    color: #fff;
    margin: 20px;
}

.card0:hover{
    margin: 0;
}
</style>