<template>
  <div class="infinite-list-wrapper1"  v-loading="creating">
    <ul
      class="infinite-list"
      v-infinite-scroll="load"
      infinite-scroll-disabled="busy">
      <li v-for="(item, index) in title" class="infinite-list-item" :key="index" @click="getDetail(index)">
            <el-card class="card1" shadow="hover" :style="cardStyleList[index%7]">
               {{item}}
            </el-card>
      </li>
    </ul>
    <p v-if="loading"><strong>加载中...</strong></p>
    <p v-if="noMore"><strong>没有更多了</strong></p>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        creating: true,
        count: 0,
        maxCnt: 1,
        loading: false,
        noMore: false,
        cardStyleList: [
            "background: linear-gradient(90deg, #3C3B3F,#605C3C);",
            "background: linear-gradient(90deg, #4CB8C4,#3CD3AD);",
            "background: linear-gradient(90deg, #DD5E89,#F7BB97);",
            "background: linear-gradient(90deg, #EB3349,#F45C43);",
            "background: linear-gradient(90deg, #11998e,#38ef7d);",
            "background: linear-gradient(90deg, #1488CC,#2B32B2);",
            "background: linear-gradient(90deg, #16A085,#F4D03F);"
        ],
        title:[],
        code:[]
      }
    },
    computed: {
      busy () {
        return this.loading || this.noMore
      }
    },
    methods: {
      load () {
        this.loading = true
        setTimeout(() => {
          let clientHeight = document.documentElement.clientHeight || document.body.clientHeight;  // 设备/屏幕高度
          let scrollTop =window.pageYOffset ||document.documentElement.scrollTop ||document.body.scrollTop; // 到头部的距离
          let scrollHeight = document.body.scrollHeight; // 滚动条的总高度
          if(scrollTop + clientHeight >= scrollHeight){
              fetch(this.$store.state.fetchUrl + '/appendMethod', {
                method:'POST',
                mode:'cors',
                body: JSON.stringify({
                  'index': this.count + 1,
                  'nums': 3
               })
              }).then(res => res.json())
              .then(data => {
                this.title.push.apply(this.title, data['name'])
                this.code.push.apply(this.code, data['code'])
                this.count += data['nums']
              })
          }
          this.loading = false
          if(this.count >= this.maxCnt) this.noMore = true
        }, 700)
      },

      getDetail: function(i){
          this.$router.push({
              path: '/detail',
              query:{
                  code: this.code[i],
                  title: this.title[i]
              }
          })
      },

    },

    mounted(){
        window.addEventListener('scroll', this.handleScroll, true);  // 监听（绑定）滚轮滚动事件
    },

    destroyed() {
        window.removeEventListener('scroll', this.handleScroll);   //  离开页面清除（移除）滚轮滚动事件
    },

    created(){
        fetch(this.$store.state.fetchUrl + '/loadMethod', {
          method:'POST',
          mode:'cors',
          body: JSON.stringify({
            'index': this.count + 1,
            'nums': 3
          })
        }).then(res => res.json())
        .then(data => {
          this.maxCnt = data['maxCnt']
          this.title.push.apply(this.title, data['name'])
          this.code.push.apply(this.code, data['code'])
          this.count += data['nums']
          setTimeout(() => {this.creating = false}, 200)
        })
    }
  }
</script>

<style>
.infinite-list-wrapper1 {
    color: #333;
    text-align: center;
    line-height: 160px;
}


.card1{
    border-radius: .3125rem;
    font-size: 1.5625rem;
    font-weight: 400;
    color: #fff;
    margin: 20px;
}

.card1:hover{
    margin: 0;
}
</style>