<template>
<div>
    <el-button-group>
        <el-button type="primary" icon="el-icon-edit" round @click="modifyCode">修改</el-button>
        <el-button type="primary" icon="el-icon-delete" round @click="deleteCode">删除</el-button>
    </el-button-group>

    <el-drawer
    :visible.sync="drawer"
    direction="btt"
    size='70%'>
        <el-input
            style="margin-left:8%; width:80%;"
            type="textarea"
            :autosize="{ minRows: 16, maxRows: 16}"
            v-model="textarea">
        </el-input>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<el-button type="primary" plain @click="submitChange">OK !</el-button>
    </el-drawer>

    <div>
    <pre  v-highlightjs>
        <code class="cpp" v-text="code">
        </code>
    </pre>
    </div>
</div>
</template>

<script> 
    export default {
        data(){
            return{
                title: '',
                code:'',
                drawer:false,
                textarea:'',
            }
        },

        methods: {
            modifyCode:function(){
                if(sessionStorage.getItem("userName") == 'River' || this.$store.getters.currentUser == 'River'){
                    this.textarea = this.code
                    this.drawer = true
                }
                else{
                       this.$message({
                        type: 'warning',
                        message: '只有博主才能修改哦 ~~'
                    })
                }
            },

            submitChange:function(){
                fetch(this.$store.state.fetchUrl + "/changeCode", {
                    method:'POST',
                    mode:'cors',
                    body: JSON.stringify({
                        'title':this.title,
                        'code':this.textarea
                    })
                }).then(res => res.text())
                .then(data => {
                    if(data == 'success'){
                       this.$message({
                        type: 'success',
                        message: '修改成功!'
                    })
                    this.code = this.textarea
                    this.drawer = false
                    }
                    else this.$message.error('出错了...');
                }).catch(err =>{
                    console.log(err)
                })
            },

            deleteCode:function(){
                if(sessionStorage.getItem("userName") != 'River' && this.$store.getters.currentUser != 'River'){
                       this.$message({
                        type: 'warning',
                        message: '只有博主才能删除哦 ~~'
                        })
                        return
                }
                this.$confirm('此操作将永久删除该代码, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {

                fetch(this.$store.state.fetchUrl + "/deleteCode", {
                    method: 'POST',
                    mode: 'cors',
                    body: JSON.stringify({
                        'title': this.title
                    })
                }).then(res => res.text())
                .then(data => {
                    if(data == 'success'){
                        this.$message({
                        type: 'success',
                        message: '删除成功!'
                    })
                    this.$router.push('/method')
                    }
                    else this.$message.error('出错了...');
                })

                }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });          
        });
            }
        },

        created(){
            this.code = this.$route.query.code
            this.title = this.$route.query.title
        },

    }
</script>

<style>
.el-drawer__header{
    margin-bottom: 0px;
}

.cpp{
    font-size: 15px!important;
    line-height: 1.5!important;
    font-family: Consolas,Monaco, monospace,Ubuntu Mono,"Source Code Pro",Courier New!important;
}
</style>