<template>
  <div>
    <el-input
    type="textarea"
    :autosize="{ minRows: 1, maxRows: 2}"
    placeholder="技巧简介"
    v-model="textarea1">
    </el-input>
    <div style="margin: 20px 0;"></div>
    <el-input
    type="textarea"
    :autosize="{ minRows: 20}"
    placeholder="代码示例"
    v-model="textarea2">
    </el-input><br><br>
    <el-button type="primary" plain @click="submitSkill">OK !</el-button>
  </div>
</template>

<script>
export default {
   data() {
     return {
       textarea1: '',
       textarea2: ''
     }
   },

    methods:{
     submitSkill:function(){
       if(this.textarea1 == '' || this.textarea2 == ''){
          this.$message({
            message: '不能为空！！',
            type: 'warning'
          })
          return
       }
       fetch(this.$store.state.fetchUrl + '/submitSkill', {
         method: 'POST',
         mode:'cors',
         body:JSON.stringify({
           'title': this.textarea1,
           'code':this.textarea2
         })
       }).then(res => res.text())
       .then(data => {
         if(data == 'success'){
            this.$message({
              message: '提交成功 ~~',
              type: 'success'
          })
          this.textarea1 = ''
          this.textarea2 = ''
         }
         else{
            this.$message.error('出错了...')
         }
       })
     }
   }

}
</script>