import Vue from 'vue'
import Router from 'vue-router'

import Method from './views/Method.vue'
import Skill from './views/Skill.vue'
import Diary from './views/Diary.vue'
import Detail from './components/Detail.vue'
import Add from './views/Add.vue'
import AddMethod from './components/AddMethod.vue'
import AddSkill from './components/AddSkill.vue'
import AddDiary from './components/AddDiary.vue'
import Words from './views/Words.vue'
import WhoAmI from './views/WhoAmI.vue'
import Message from './views/Message.vue'
import Search from './views/Search.vue'
import Login from './views/Login.vue'

Vue.use(Router)

export default new Router({
  routes: [{
      path: '*',
      redirect: '/login',
    },{
      path: '/method',
      name:'method',
      component: Method,
      meta:{needLogin:true, needRoot:false}
    },{
      path: '/skill',
      name: 'skill',
      component: Skill,
      meta:{needLogin:true, needRoot:false}
    },{
      path: '/diary',
      name: 'diary',
      component: Diary,
      meta:{needLogin:true, needRoot:true}
    },{
      path: '/detail',
      name: 'detail',
      component: Detail,
      meta:{needLogin:true, needRoot:false}
    },{
      path: '/add',
      name: 'add',
      component: Add,
      meta:{needLogin:true, needRoot:true},
      children:[{
        path: '/add',
        name: 'addmethod',
        component: AddMethod
      },{
        path: '/add/skill',
        name: 'addskill',
        component: AddSkill
      },{
        path: '/add/diary',
        name: 'adddiary',
        component: AddDiary
      }]
    },{
      path: '/words',
      name: 'words',
      component: Words,
      meta:{needLogin:true, needRoot:false}
    },{
      path: '/whoami',
      name: 'whoami',
      component: WhoAmI,
      meta:{needLogin:true, needRoot:false}
    },{
      path: '/message',
      name: 'message',
      component: Message,
      meta:{needLogin:true, needRoot:false}
    },{
      path:'/search',
      name: 'search',
      component: Search,
      meta:{needLogin:false, needRoot:false}
    },{
      path:'/login',
      name: 'login',
      component: Login,
    }
  ]
})
