export const userStatus = (state, userform) => {
    if(userform && userform.currentUser != "null"){
        state.userform.currentUser = userform.username
        state.userform.avatarID = userform.avatarID
        state.isLogin = true
    }
    else if(userform == null){
        sessionStorage.setItem("userName", null)
        sessionStorage.setItem("avatarID", 0)
        state.userform.currentUser = null
        state.userform.avatarID = 0
        state.isLogin = false
    }
}