<template>
<div>
  <el-radio-group v-model="radio" @change="changeHandler">
    <el-radio :label="1">算法</el-radio>
    <el-radio :label="2">技巧</el-radio>
    <el-radio :label="3">日记</el-radio>
  </el-radio-group>
  <el-divider></el-divider>
  <router-view></router-view>
</div>
</template>

<script>
  export default {
    data () {
      return {
        radio: 1
      };
    },

    methods: {
        changeHandler(val){
            if(val==1){
                this.$router.push({
                    path: '/add'
                })
            }
            else if(val==2){
                this.$router.push({
                    path: '/add/skill'
                })
            }
            else if(val==3){
                this.$router.push({
                    path: '/add/diary'
                })
            }
        }
    },

  }
</script>