<template>
  <div>
    <el-input
    type="textarea"
    :autosize="{ minRows: 10}"
    placeholder="随便写点什么吧 ~~"
    v-model="textarea">
    </el-input><br><br>
    <el-button type="primary" plain @click="submitDiary">OK !</el-button>
  </div>
</template>

<script>
export default {
   data() {
     return {
       textarea: '',
     }
   },

   methods:{
     submitDiary:function(){
       if(this.textarea == ''){
          this.$message({
            message: '不能为空！！',
            type: 'warning'
          })
          return
       }
       var reg = new RegExp("\n","g");
       this.textarea = this.textarea.replace(reg, "<br>");

       fetch(this.$store.state.fetchUrl + '/submitDiary', {
         method: 'POST',
         mode:'cors',
         body:JSON.stringify({
           'content': this.textarea
         })
       }).then(res => res.text())
       .then(data => {
         if(data == 'success'){
            this.$message({
              message: '提交成功 ~~',
              type: 'success'
          })
          this.textarea = ''
         }
         else{
            this.$message.error('出错了...')
         }
       })
     }
   }
}
</script>