<template>
    <div v-loading="creating">
        <el-container class="card-container" v-show="!isLogin">

          <div>
            <div style="width:500px;" @click="loginClick">
            <transition name="el-fade-in-linear">
                <el-card class="login-card" shadow="hover" :style="loginStyle" :value="logining">
                    <transition name="el-fade-in-linear">
                    <div v-if="registershow" style="line-height:57px;margin:60px;">
                    <el-input
                    placeholder="取个昵称吧"
                    prefix-icon="el-icon-user"
                    v-model="newNickname"
                    clearable="">
                    </el-input>
                    <el-input
                    placeholder="密码"
                    v-model="newPassword1"
                    show-password>
                    </el-input>
                    <el-input
                    placeholder="再输一次密码"
                    v-model="newPassword2"
                    show-password
                    @keyup.enter.native="register">
                    </el-input><br>
                    <el-button type="primary" icon="el-icon-check" circle @click="register"></el-button>
                    </div>
                    </transition>
                    <transition>
                    <div v-if="!registershow">登 录</div>
                    </transition>
                </el-card>
            </transition>
            </div>
          </div>


          <div>
            <div @click="registerClick">
            <transition name="el-fade-in-linear">
                <el-card class="register-card" shadow="hover" :style="registerStyle">
                    <transition name="el-fade-in-linear">
                    <div v-if="loginshow" style="line-height:80px;margin:60px;">
                    <el-input
                    placeholder="昵称"
                    prefix-icon="el-icon-user"
                    v-model="nickname"
                    clearable
                    @keyup.enter.native="login">
                    </el-input>
                    <el-input
                    placeholder="密码"
                    v-model="password"
                    show-password
                    @keyup.enter.native="login">
                    </el-input><br>
                    <el-button id="login-button" type="warning" icon="el-icon-check" circle @click="login"></el-button>
                    </div>
                    </transition>
                    <transition>
                    <div v-if="!loginshow">注 册</div>
                    </transition>
                </el-card>
            </transition>
            </div>
          </div>
          
        </el-container>
        <div v-show="isLogin">
        <div class="hello">
                <el-container>
                    <el-aside class="button-container">
                        <el-button class="left-switch-button" type="primary" icon="el-icon-arrow-left" circle @click="turnLeft" :disabled="userform.avatarID==0"></el-button>
                    </el-aside>
                    <div class="avatar-container">
                        <el-avatar id="avatar" :size="200" :src="avatar"></el-avatar>
                    </div>
                    <el-aside  class="button-container">
                        <el-button class="right-switch-button" type="primary" icon="el-icon-arrow-right" circle @click="turnRight" :disabled="userform.avatarID==maxID"></el-button>
                    </el-aside>
                </el-container>
            <el-container>
                <el-header style="margin-top:15px;margin-left:30px;">{{greeting}}&nbsp;&nbsp;&nbsp;{{userform.username}}</el-header>
                <el-container>
                <el-button style="margin-left:390px;margin-top:30px; margin-right:30px;" round type="primary" @click="submitAvatarID" :disabled="disabled">确认头像</el-button>
                <el-button style="margin-top:30px;" round type="primary" @click="logout">Logout</el-button>
                </el-container>
            </el-container>
        </div>
        </div>
    </div>
</template>

<script>
import { setTimeout } from 'timers';
export default {
    inject:['reload'],
    data(){
        return {
            creating:false,
            disabled:true,
            loginClickable: true,
            registerClickable:true,
            logining: false,
            registering: false,
            loginshow: false,
            registershow:false,
            nickname:'',
            password:'',
            newNickname:'',
            newPassword1:'',
            newPassword2:'',
            registerStyle:'',
            registerOldStyle:"text-align: center;line-height: 320px;border-radius: 5.3125rem;font-size: 2.5625rem;color: #fff;margin: 10px;background: linear-gradient(90deg, #1488CC,#2B32B2);width: 450px;height: 400px;",
            registerNewStyle:"text-align: center;line-height: 320px;border:solid #F45C43;border-width:3px;border-radius: 5.3125rem;font-size: 2.5625rem;color: #fff;margin: 10px;background:white;width: 450px;height: 400px;",
            loginStyle:'',
            loginOldStyle:"text-align: center;line-height: 320px;border-radius: 5.3125rem;font-size: 2.5625rem;color: #fff;margin: 10px;background: linear-gradient(90deg, #EB3349,#F45C43);width: 450px;height: 400px;",
            loginNewStyle:"text-align: center;line-height: 320px;border:solid #1488CC;border-width:3px;border-radius: 5.3125rem;font-size: 2.5625rem;color: #fff;margin: 10px;background:white;width: 450px;height: 400px;",

            
            maxID: 52,
            userform:{
                username:'River',
                avatarID: 0,
            },
            
        }
    },

    computed:{
        isLogin(){
            if(sessionStorage.getItem("userName")){
                console.log(sessionStorage.getItem("userName"))
                var user={
                    'username': sessionStorage.getItem("userName"),
                    'avatarID': sessionStorage.getItem("avatarID")
                }
                this.userform.username = sessionStorage.getItem("userName")
                this.userform.avatarID = sessionStorage.getItem("avatarID")
                this.$store.commit("userStatus", user)
            }
            else this.$store.commit("userStatus", null)
            return this.$store.getters.isLogin && sessionStorage.getItem("userName")!=String('null')
        },

        avatar(){
            console.log(this.userform.avatarID)
            return require("../assets/avatar/图片"+ this.userform.avatarID.toString() +".jpg")
        },

        greeting(){
            var now = new Date()
            var hour = now.getHours()
            if(hour < 10 && hour >= 1) return 'Good morining ~~'
            if(hour < 14) return 'Good day ~~'
            if(hour < 18) return 'Good afternoon ~~'
            if(hour < 21) return 'Good evening ~~'
            return 'Good night ~~'
        }
    },

    methods: {
        loginClick:function(){
            if(this.loginClickable == false) return
            if(this.logining == false){
                this.registerStyle = this.registerNewStyle
                this.loginshow = true
                this.logining = true
                this.registerClickable = false
            }
            else{
                this.loginshow = false
                this.logining = false
                this.registerStyle = this.registerOldStyle
                this.registerClickable = true
            }
        },

        registerClick:function(){
            if(this.registerClickable == false) return
            if(this.registering == false){
                this.loginStyle = this.loginNewStyle
                this.registershow = true
                this.registering = true
                this.loginClickable = false
            }
            else{
                this.registershow = false
                this.registering = false
                this.loginStyle = this.loginOldStyle
                this.loginClickable = true
            }
        },

        register:function(){
            if(this.newNickname == '' || this.newPassword1 == '' || this.newPassword2 == ''){
                this.$message({
                    message: '不能为空！！',
                    type: 'warning'
                })
                return
            }
            if(this.newPassword1 != this.newPassword2){
                this.$message({
                    message: '两次密码不一致！！',
                    type: 'warning'
                })
                return
            }
            fetch(this.$store.state.fetchUrl + '/register', {
                method: 'POST',
                mode:'cors',
                body:JSON.stringify({
                    'nickname': this.newNickname,
                    'password': this.newPassword1
                })
            }).then(res => res.text())
            .then(data => {
                if(data == 'already-exist'){
                    this.$message({
                        message: '该昵称已被注册 ...',
                        type: 'warning'
                    })
                }
                else if(data == 'success'){
                    this.$message({
                        message: '注册成功！！',
                        type: 'success'
                    })
                    setTimeout(()=>{this.registerClick(), this.loginClick()}, 1000)
                }
                else{
                    this.$message.error('出错了...')
                }
            })
        },

        login:function(){
            if(this.nickname == '' || this.password == ''){
                this.$message({
                    message: '不能为空！！',
                    type: 'warning'
                })
                return
            }
            this.creating = true

            fetch(this.$store.state.fetchUrl + '/login', {
                method: 'POST',
                mode:'cors',
                body:JSON.stringify({
                    'nickname': this.nickname,
                    'password': this.password
                })
            }).then(res => res.json())
            .then(data => {
                if(data['data'] == 'noNickname'){
                    this.$message({
                        message: '该昵称不存在 ...',
                        type: 'warning'
                    })
                }
                else if(data['data'] == 'wrong'){
                    this.$message({
                        message: '密码错误！！',
                        type: 'warning'
                    })
                }
                else if(data['data'] == 'success'){
                    this.$message({
                        message: '登录成功！！',
                        type: 'success'
                    })
                    this.userform.avatarID = data['avatarID']
                    sessionStorage.setItem("userName", this.nickname)
                    sessionStorage.setItem("avatarID", this.userform.avatarID)
                    var user={
                        'username': this.nickname,
                        'avatarID': this.userform.avatarID
                    }
                    this.$store.dispatch("setUser", user)
                    this.userform.username = this.nickname
                    this.loginClick()
                    this.reload()
                }
                else{
                    this.$message.error('出错了...')
                }
            })
            setTimeout(()=>{this.creating=false}, 500)
        },

        logout:function(){
            this.$confirm('确认退出?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                    type: 'warning'
            }).then(() => {
                this.$store.dispatch("setUser", null)
                sessionStorage.clear();
                this.$message({
                    type: 'success',
                    message: '退出成功！!'
                });
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消'
                });          
            });
        },

        turnLeft:function(){
            this.disabled = false
            this.userform.avatarID -= 1
            document.getElementById('avatar').setAttribute('style', 'width:200px; height:200px;border: 4px dashed rgb(78, 129, 224); margin-bottom:0px;')
        },

        turnRight:function(){
            this.disabled = false
            this.userform.avatarID = Number(this.userform.avatarID) + 1
            document.getElementById('avatar').setAttribute('style', 'width:200px; height:200px;border: 4px dashed rgb(78, 129, 224); margin-bottom:0px;')
        },

        submitAvatarID:function(){
            fetch(this.$store.state.fetchUrl + "/submitAvatarID", {
                method:'POST',
                mode:'cors',
                body:JSON.stringify({
                    'avatarID':this.userform.avatarID,
                    'nickname':this.$store.getters.currentUser
                })
            }).then(res => res.text())
            .then(data => {
                if(data == "success"){
                    this.$message({
                        type: 'success',
                        message: '头像更改成功 ~~'
                    }); 
                    this.disabled = true
                    document.getElementById('avatar').setAttribute('style', 'width:200px; height:200px;border: 4px solid rgb(78, 129, 224); margin-bottom:0px;')
                    sessionStorage.setItem("avatarID", this.userform.avatarID)
                    var user={
                        'username': this.nickname,
                        'avatarID': this.userform.avatarID
                    }
                    this.$store.dispatch("setUser", user)
                }
                else{
                    this.$message.error('出错了...')
                }
            })
        }
    },

    created(){
        this.registerStyle = this.registerOldStyle
        this.loginStyle = this.loginOldStyle
    }
}
</script>

<style>
.card-container{
    height: 460px;
    overflow: hidden;
    font-weight: 300;
}

.register-card .el-input__inner {
    border-color: #F45C43;
}

#login-button{
    border:1px solid #F45C43;
    background-color: #F45C43;
}

#login-button:hover{
    border:1px solid rgb(243, 126, 108);
    background-color: rgb(243, 126, 108);
}

.hello{
    overflow: hidden;
    font-weight: 300;
    font-size: 2.5rem;
    text-align:center;
    font-family:'Mirages Custom', 'Merriweather', 'Open Sans', 'PingFang SC', 'Hiragino Sans GB', 'Microsoft Yahei', 'WenQuanYi Micro Hei',  'Segoe UI Emoji', 'Segoe UI Symbol', Helvetica, Arial, sans-serif;
}

.button-container{
    width:30%;
    height:100%;
}

.avatar-container{
    width:40%;
    height:100%;
}

#avatar{
    width:200px; 
    height:200px;
    border: 4px solid rgb(78, 129, 224);
    margin-bottom:0px;
}
#avatar:hover{
    -webkit-transform:rotate(45deg);
    transform:rotate(45deg);
    -webkit-transition:-webkit-transform .5s linear;
    transition:transform .5s linear;
}

.left-switch-button{
    margin-left: 250px;
    margin-right:10px;
    margin-top: 80px;
}

.right-switch-button{
    margin-left:10px;
    margin-right:250px;
    margin-top: 80px;
}
</style>