<template>
  <div>
    <el-input
    type="textarea"
    style="width:30%"
    :autosize="{ minRows: 1, maxRows: 2}"
    placeholder="算法名称"
    v-model="textarea0">
    </el-input><span v-for="i in 5" :key="i">&nbsp;</span>
    <el-input
    type="textarea"
    style="width:60%"
    :autosize="{ minRows: 1, maxRows: 2}"
    placeholder="简要说明"
    v-model="textarea1">
    </el-input>
    <div style="margin: 20px 0;"></div>
    <el-input
    type="textarea"
    :autosize="{ minRows: 20}"
    placeholder="代码模板"
    v-model="textarea2">
    </el-input><br><br>
    <el-button type="primary" plain @click="submitMethod">OK !</el-button>
  </div>
</template>

<script>
export default {
   data() {
     return {
       textarea0: '',
       textarea1: '',
       textarea2: ''
     }
   },

   methods:{
     submitMethod:function(){
       if(this.textarea1 == '' || this.textarea2 == '' || this.textarea0 == ' '){
          this.$message({
            message: '不能为空！！',
            type: 'warning'
          })
          return
       }
       fetch(this.$store.state.fetchUrl + '/submitMethod', {
         method: 'POST',
         mode:'cors',
         body:JSON.stringify({
           'title': '【' + this.textarea0 + '】 ' + this.textarea1,
           'code':this.textarea2
         })
       }).then(res => res.text())
       .then(data => {
         if(data == 'success'){
            this.$message({
              message: '提交成功 ~~',
              type: 'success'
          })
          this.textarea0 = ''
          this.textarea1 = ''
          this.textarea2 = ''
         }
         else{
            this.$message.error('出错了...')
         }
       })
     }
   }
}
</script>