<template>
  <div class="diary-page" v-loading="creating">
    <ul>
      <li v-for="(item, index) in content" :key="index">
            <el-card class="card3" :style="cardStyleList[index%7]">
                <div slot="header" class="clearfix">
                  <span><strong>{{date[index]}}</strong></span>
                  <el-button class="delete-button" type="text" icon="el-icon-delete" @click="deleteDiary(index)"></el-button>
                </div>
                <div v-html="item"></div>
            </el-card>
      </li>
    </ul>
    <el-pagination
      @current-change="handleCurrentChange"
      :current-page.sync="currentPage"
      :page-size="pageSize"
      background
      layout="prev, pager, next"
      :total="totalNum">
    </el-pagination>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        creating: true,
        currentPage: 1,
        totalNum: 1,
        pageSize: 7,
        cardStyleList: [
            "border-color: #4CB8C4;",
            "border-color: #3C3B3F;",
            "border-color: #DD5E89;",
            "border-color: #EB3349;",
            "border-color: #11998e;",
            "border-color: #1488CC;",
            "border-color: #16A085;"
        ],
        content:[],
        date:[]
      }
    },

    methods: {
      handleCurrentChange: function(){
        this.creating = true
        fetch(this.$store.state.fetchUrl + "/appendDiary", {
          method: 'POST',
          mode: 'cors',
          body: JSON.stringify({
            'start': (this.currentPage - 1) * this.pageSize,
            'pageSize': this.pageSize
          })
        }).then(res => res.json())
        .then(data => {
          this.date.splice(0, this.pageSize)
          this.date.push.apply(this.date, data['time'])
          this.content.splice(0, this.pageSize)
          this.content.push.apply(this.content, data['content'])
          setTimeout(() => {this.creating = false}, 200)
        })
      },

      deleteDiary: function(i){
        this.$confirm('此操作将永久删除该随笔, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {

        fetch(this.$store.state.fetchUrl + "/deleteDiary", {
          method: 'POST',
          mode: 'cors',
          body: JSON.stringify({
            'time': this.date[i]
          })
        }).then(res => res.text())
        .then(data => {
            if(data == 'success'){
              this.$message({
                type: 'success',
                message: '删除成功!'
              })
              this.totalNum -= 1
              this.handleCurrentChange()
            }
            else this.$message.error('出错了...');
        })

        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
      }

    },

    created(){
      fetch(this.$store.state.fetchUrl + "/loadDiary", {
        method: 'POST',
        mode: 'cors',
        body: JSON.stringify({
          'pageSize': this.pageSize
        })
      }).then(res => res.json())
      .then(data => {
        this.totalNum = data['totalNum']
        this.date = data['time']
       this.content = data['content']
        setTimeout(() => {this.creating = false}, 200)
      })
    }
  }
</script>

<style>
.diary-page {
    color: #333;
    line-height: 20px;
    margin-right: 100px;
}

.card3{ 
    border-radius: .8125rem;
    font-size: 0.9625rem;
    font-weight: 400;
    color: rgb(94, 92, 92);
    margin: 20px;
}


.el-card__header {
    padding: 10px 25px;
}
.delete-button{
  float: right; 
  padding: 3px 0;
}

.delete-button:hover{
  color: rgb(0, 0, 0);
}

.el-pagination{
    text-align: center;
}
</style>