import Vue from 'vue'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css' 
import App from './App.vue'
import router from './router'
import VueHighlightJS from 'vue-highlightjs'
import 'highlight.js/styles/atom-one-dark.css'
import store from './vuex/store'
import { Message } from 'element-ui';

Vue.config.productionTip = false
Vue.use(ElementUI)
Vue.use(VueHighlightJS)

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')

router.beforeEach((to, from, next) => {
    if(to.matched.some(item => item.meta.needLogin)){
      if(to.path == '/login') next()
      if(to.matched.some(item => item.meta.needRoot)){
        if(sessionStorage.getItem("userName") == 'River' || store.getters.currentUser == 'River') return next()
        
        Message({
           type: 'warning',
           message: '只有博主才能进入哦 ...'
        })
        return next({path: "/login"});
      }
      else{
        console.log(store.state)
        if(store.getters.isLogin && store.getters.currentUser!="null") return next()
        Message({
          type: 'warning',
          message: '请先登录！！'
       })
       return next({path: "/login"});
      }
    }
    next()
})