<template>
  <el-container>
  <el-header height="250px" id="header">River's Space</el-header>
  <el-container>
    <el-aside class="big-aside" width="280px"><mynav></mynav></el-aside>
    <el-main>
      <router-view v-if="isRouterAlive"></router-view>
    </el-main>
  </el-container>
  <el-footer height="85px">Copyright @ 2019 River</el-footer>
</el-container>
</template>

<script>
import mynav from "./components/Navbar.vue"

export default {
  name: "App",
  components : {
    mynav
  },
  provide () {
    return {
      reload: this.reload
    }
  },
  data () {
    return {
      isRouterAlive: true
    }
  },
  methods: {
    reload () {
      this.isRouterAlive = false
      this.$nextTick(function () {
        this.isRouterAlive = true
      })
    },
  }
}

</script>

