              <span slot="title">{{ item.gameItem }}</span>
            </el-menu-item>
          </el-menu-item-group>
      </el-submenu>

      <el-menu-item v-for="(item, i) in navList2" :key="i+20" :index="item.name">
        <i :class="item.class"></i>
        <span slot="title">{{ item.navItem }}</span>
      </el-menu-item>

