<script>

  export default {
    data() {
      return {
        navList1:[
            {name:'/login', navItem:'登录', class:'el-icon-user'},
            {name: '/search', navItem:'搜索', class:'el-icon-zoom-in'},
            {name:'/add', navItem:'增添', class:'el-icon-upload2'},
            {name:'/method', navItem:'算法', class:'el-icon-star-on'},
            {name:'/skill', navItem:'技巧', class:'el-icon-magic-stick'},
            {name:'/diary', navItem:'日记', class:'el-icon-edit'},
