        gameList:[
            {name:'/words', gameItem:'猜单词？', class:'el-icon-question'},
            {name:'/whoami', gameItem:'我是谁？', class:'el-icon-s-help'}
        ],
        navList2:[
            {name:'/message', navItem:'留言板', class:'el-icon-edit-outline'}
        ]
      }
    },

    methods: {
      handleOpen(key, keyPath) {
        this.active = this.$route.path;
        console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath);
      }
    }
  }
